
import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch";
import { tiemDate } from "../helpers/timeDate";
import { types } from "../types/types";

export const StartfillEvents = ()=>{
    return async(dispatch) =>{
        try {
            const resp= await fetchConToken('events');
            const body = await resp.json();
            // console.log(body.eventos)
            const eventos = tiemDate( body.eventos )
            // console.log(eventos)
            dispatch(fillEvents(eventos))
            
        } catch (error) {
            console.log(error)
        }
    }
}

export const startAddNew = (evento)=>{
    return async(dispatch, getState) => {

        const {uid,name}=getState().auth;

        const resp= await fetchConToken('events',evento,'POST');
        const body= await resp.json();
        if(body.ok){
            evento.id=body.evento.id;
            evento.user={
                _id:uid,
                name
            }
            dispatch(eventAddNew(evento))
        }

    }
}

const fillEvents = (eventos)=>({
    type:types.eventFillEvents,
    payload:eventos
})

export const eventAddNew = ( event ) => ({
    type: types.eventAddNew,
    payload: event,
})

export const eventActive = ( event ) => ({
    type: types.eventActive,
    payload: event
})

export const eventClearActive = () => ({
    type:types.eventClearActive
})


export const eventStartDelete = () => {
    return async (dispatch,getState) =>{
        const {id}= getState().calendar.activeEvent
        try {
            const resp = await fetchConToken(`events/${id}`, {}, 'DELETE')
            const body = await resp.json()
            if(body.ok){
                dispatch(eventDelete())
            }
            else{
                Swal.fire('Error',body.msg,'error')
            }
            
        } catch (error) {
            console.log(error)
        }
    }
}

export const eventDelete = () => ({
    type:types.eventDelete
})

export const eventStartUpdate = (event)=>{
    return async(dispatch) =>{
        try {
            const resp = await fetchConToken(`events/${event.id}`, event, 'PUT')
            const body = await resp.json()
            if(body.ok){
                dispatch(eventUpdate(event))
            }
            else{
                Swal.fire('Error',body.msg,'error')
            }
            
        } catch (error) {
            console.log(error)
        }
    }
}
export const cleanEvents = ()=>({
    type:types.eventCleanEvents
})
const eventUpdate = (event) => ({
    type : types.eventUpdate,
    payload : event
})