import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import './modal.css'
import moment from 'moment';
// import { useState } from 'react';
// import { useForm } from '../../hooks/useForm';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { uiCloseModal } from '../../actions/ui';
import { eventClearActive, eventStartUpdate, startAddNew } from '../../actions/events';

const customStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)'
    }
  };
  Modal.setAppElement('#root')

  const now=moment().minute(0).second(0).add(1,'hours')

  const initialState={
    title: '',
    notes: '',
    start:now.toDate(),
    end:now.add(30,'minutes').toDate()
};
export const CalendarModal = () => {

    const dispatch = useDispatch()
    const {modalOpen} = useSelector(state => state.ui)
    const [dateStart, setDateStart] = useState( now.toDate() )
    const [titleWarn, setTitleWarn] = useState(false)
    const {activeEvent} = useSelector(state => state.calendar)
    
    const [formValues, setFormValues] = useState(initialState)
    useEffect(() => {
        if(activeEvent){
           
            setFormValues(activeEvent)
        }
        else{
            setFormValues(initialState)
        }
        
        
    }, [activeEvent,setFormValues])
    const {title, notes, start, end}=formValues;
    console.log(formValues)



   

    const handleInputChange = ({ target }) =>{
        // console.log(target)
       setFormValues({
           ...formValues,
           [target.name]: target.value
       })
    }
    

    const closeModal= ()=>{
        dispatch(uiCloseModal());
        dispatch(eventClearActive());
        setFormValues(initialState);
    }
    const handleStartChange=(e)=>{
        setDateStart(e);
        setFormValues({
            ...formValues,
            start: e
        })
       

    }
    const handleEndChange=(e)=>{
        setFormValues({
            ...formValues,
            end: e
        })
        
    }
    const handleSubmit = (e)=>{
        e.preventDefault();
        const momentStart = moment(start);
        const momentEnd = moment(end);
        
        if(momentStart.isSameOrAfter(momentEnd)){
            Swal.fire('Error','La Fecha de fin debe ser mayor que la de inicio', 'error')
            return;
        }   

        if(title.trim().length < 2){
            setTitleWarn(true)
            
        }else{
            setTitleWarn(false)

        }

        if(activeEvent&&activeEvent.id){
            dispatch(eventStartUpdate({...formValues}))
        }else{

            dispatch(startAddNew({
                ...formValues
                
            }))
        }

        dispatch(uiCloseModal())
        setFormValues(initialState)
        dispatch(eventClearActive())
        

        
    }
    
    return (
        <Modal
          isOpen={modalOpen}
        //   onAfterOpen={afterOpenModal}
          onRequestClose={closeModal}
          closeTimeoutMS={200}
          style={customStyles}
          className="modal"
          overlayClassName="modal-fondo"
        >
           <h1> {(activeEvent&&activeEvent.id)?'Editar Evento':'Nuevo evento'} </h1>
            <hr />
            <form onSubmit={handleSubmit} className="container">

                <div className="form-group">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker
                        onChange={handleStartChange}
                        value={start}
                        name="start"
                        className="form-control"
                    />
                </div>

                <div className="form-group">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker
                        onChange={handleEndChange}
                        value={end}
                        name="end"
                        minDate={dateStart}
                        className="form-control"
                    />
                </div>

                <hr />
                <div className="form-group">
                    <label>Titulo y notas</label>
                    <input 
                        type="text" 
                        className={ `form-control ${titleWarn && 'is-invalid'}` }
                        placeholder="Título del evento"
                        name="title"
                        autoComplete="off"
                        value={title}
                        onChange={handleInputChange}
                    />
                    <small id="emailHelp" className="form-text text-muted">{titleWarn ? 'debe tener almenos 2 caracteres' : 'Una descripción corta'}</small>
                </div>

                <div className="form-group">
                    <textarea 
                        type="text" 
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="notes"
                        value={notes}
                        onChange={handleInputChange}
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Información adicional</small>
                </div>

                <button
                    type="submit"
                    className="btn btn-outline-primary btn-block"
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>

            </form>
        </Modal>
    )
}
