import React, { useEffect } from 'react'
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavScreen } from '../ui/NavScreen'
import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'

import "react-big-calendar/lib/css/react-big-calendar.css"

import { messages } from '../../helpers/messages-es'
import 'moment/locale/es'

import { CalendarEvent } from './CalendarEvent'
import { CalendarModal } from './CalendarModal'
import { uiOpenModal } from '../../actions/ui'
import { eventActive, eventClearActive,  StartfillEvents } from '../../actions/events'
import { AddNewFab } from '../ui/AddNewFab'
import { DeleteEventFab } from '../ui/DeleteEventFab'

moment.locale('es')
const localizer = momentLocalizer(moment);



export const CalendarScreen = () => {

    
    const dispatch = useDispatch();
    
    const {events,activeEvent} = useSelector(state => state.calendar)
    const {uid} = useSelector(state => state.auth)
    
    const [lastView, setLastView] = useState(localStorage.getItem('lastView')||'month')

    useEffect(() => {
        dispatch(StartfillEvents())
    }, [dispatch])
    
    const eventStyleGetter = ( event, start, end, isSelescted)=>{
        const style = {
            backgroundColor : (uid=== event.user._id)? '': '#aaa',
            color: "white",
            zIndex:'0', 
        }

        return {style}
    }

    const onViewChange=(e)=>{
        setLastView(e);
        localStorage.setItem('lastView',e);
    }
    const onDoubleClickEvent = (e)=>{
        dispatch(uiOpenModal())
    }
    const onSelectEvent = (e)=>{
        // console.log(e.end)
        dispatch(eventActive(e))
    }
    const onSelectSlot = (e)=>{

        dispatch(eventClearActive())
        
        dispatch(eventActive({
            title:'',
            notes:'',
            start:e.start,
            end:e.end
        }))
        dispatch(uiOpenModal());

    }

    return (
        <div className="calendar-screen">
            <NavScreen/>

            <Calendar
                localizer={localizer}
                events={events}
                startAccessor="start"
                endAccessor="end"
                messages={messages}
                eventPropGetter={eventStyleGetter}
                onView={onViewChange}
                onDoubleClickEvent={ onDoubleClickEvent }
                onSelectEvent={ onSelectEvent }
                onSelectSlot={ onSelectSlot }
                selectable={true}
                view={lastView}
                components={{
                    event:CalendarEvent
                }}
            />
            {
                (activeEvent)&&
                <DeleteEventFab/>
                
            }
            <AddNewFab/>
            <CalendarModal/>

        </div>
    )
}
